/* ------------------------------------------------------------------------ */
/* hugo-tufte.css */
/* Contains extensions to the original tufte.css styles to */
/* accomodate a blog-like site. */
/* ------------------------------------------------------------------------ */

/* ------------------------------------------------------------------------ */
/* Generic content, such as the index list pages */
/* ------------------------------------------------------------------------ */
:root * {
  text-align: left;
  text-rendering: optimizeLegibility;
}

:focus {
  outline: medium auto currentColor;
  outline: medium auto invert;
  outline: 5px auto -webkit-focus-ring-color;
}

::selection {
  color: #fffff8;
  background-color: #404040;
}

body {
  width: 90%;
  max-width: 1350px;
}

@media (max-width: 760px) {
  body {
    width: 100%;
    margin: 0;
  }

  article {
    padding-top: 3rem;

    &:not(.home-page) > section:first-of-type {
      padding-top: 0;
    }
  }
}

a.heading-anchor {
  display: none;
  visibility: collapse;
}

h2 {
  margin-top: 5.5rem;
}

h2:hover > a.heading-anchor,
h3:hover > a.heading-anchor {
  display: inline;
  visibility: visible;
}

hr {
  text-align: left;
  margin-left: 0;
  margin-top: 1.4rem;
  margin-bottom: 1.4rem;
  width: 75%;
  max-width: 45rem;
  border-style: solid none none none;
  border-color: #111;
}

kbd {
  border: 1px #111 solid;
  border-radius: 5px;
  padding-right: 2px;
  padding-left: 2px;
}

mark {
  background: #f0d9bb;
}

blockquote {
  padding: 1rem 0 1rem 2rem;
  border-left: 1px solid #999;
  margin-left: 0;
  position: relative;
}

blockquote > *:first-child {
  margin-top: 0;
}
blockquote > *:last-child {
  margin-bottom: 0;
}

section > blockquote {
  /* Make blockquotes themselves, not the content within them, narrow. */
  width: 55%;
}
@media (max-width: 760px) {
  section > blockquote {
    width: 100%;
  }
}

section > blockquote > * {
  width: 100%;
}

.epigraph blockquote footer {
  text-align: right;
}

section > div {
  font-size: 1.4rem;
  width: 90%;
}

/* ------------------------------------------------------------------------ */
/* Styling for listing pages. */
/* ------------------------------------------------------------------------ */
.list-section {
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 87.5%;
    max-width: 45rem;
  }
  li {
    margin: 0;
    /*font-size: 95%;*/
  }
}
.list-section .list-date {
  display: inline-block;
  font-size: 0.75em;
  width: 13ch;
  /* padding-right: 2em; */
  /* margin-right: 2em; */
}

table:not(.lntable) {
  border-collapse: collapse;
  margin: 1.4em 0;
  font-size: 1.4rem;
  width: auto; /* making booktabs style tables the unstyled default in case someone uses Markdown styling */
  /* margin: 0 auto; */
  /* border-spacing: 0px; */
}

table:not(.lntable) thead {
  border-bottom: 1px solid #111;
}

table:not(.lntable) tr {
}

table:not(.lntable) tr th,
table:not(.lntable) tr td {
  padding: 1rem 0 0;
  line-height: 1.4rem;
}

table.lntable {
  td.lntd {
    padding: 0em;
  }
  border-spacing: 0;
  padding: 0;
}

/* ------------------------------------------------------------------------ */
/* Styling for footnotes. */
/* ------------------------------------------------------------------------ */
.footnotes hr {
  margin-top: 4.4em;
}

.footnotes ol {
  width: 55%;
  margin-top: 4.4rem;

  li {
    p {
      width: 100%;
      margin: 0;
      padding: 0;

      a.footnote-backref {
        font-size: 1.2rem;
        text-decoration: none;
      }
    }
  }
}

/* ------------------------------------------------------------------------ */
/* Styling for maths. */
/* ------------------------------------------------------------------------ */

/* mjx-container[jax="CHTML"][display="true"]::-webkit-scrollbar,
.katex-display::-webkit-scrollbar {
  display: none;
}*/

mjx-container[jax="CHTML"][display="true"],
.katex-display {
  overflow: auto;
  overflow-y: hidden;
  /*     -ms-overflow-style: none;
    scrollbar-width: none; */
}

/* ------------------------------------------------------------------------ */
/* Styling for margin/side notes. */
/* ------------------------------------------------------------------------ */
.marginnote .marginnote-ind,
.sidenote .sidenote-number {
  margin-right: 5px;
}

/* prevent over-crowding */

.sidenote,
.marginnote {
  margin-bottom: 1rem;
}

.sidenote code,
.marginnote code {
  font-size: 0.9rem;
}

@media (max-width: 760px) {
  .marginnote-ind .ind {
    font-size: 1.5em;
    line-height: 1;
  }

  label.margin-toggle {
    display: inline-block;
    border: 1px solid #111;
    padding: 0 0.5ch;
    margin: 0 0.5ch;
    text-align: center;
    border-radius: 0.5ch;
    font-family: monospace;

    &:hover {
      cursor: pointer;
    }

    &::before {
      content: "+";
      margin-right: 0.2em;
      top: 0;
      left: 0;
    }

    &:has(+ :checked) {
      &::before {
        content: "-";
        top: 0;
        left: 0;
      }
    }
  }
}

/* ------------------------------------------------------------------------ */
/* Styling & fixes for cols. */
/* ------------------------------------------------------------------------ */
// create grid
.row {
  /* mobile */
  display: grid;
  width: 75%;
  gap: 0.3rem;
  margin: 0 0 1rem 0;
}

.row .column:nth-of-type(2n) {
  margin-left: 1rem;
}

.row .column:nth-of-type(3n) {
  margin-left: 2rem;
}

.row .column > p:first-child {
  margin-top: 0;
}

.row .column > p:last-child {
  margin-bottom: 0;
}

@media (min-width: 760px) {
  /* desktop */
  /* https://stackoverflow.com/a/62086985 */
  .row {
    grid-auto-columns: 1fr;
    grid-auto-flow: column;
    /* above 2 lines are equivalent as below 1 line */
    /* grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); */
    gap: 1.4rem;
    margin: 0 0 1.4rem 0;
  }
  .row .column {
    margin-left: 0 !important;
  }
}

.column > p {
  width: 100%;
}

.column > p > label.margin-toggle.marginnote-ind {
  display: inline;
}

.column > p > .sidenote,
.column > p > .marginnote {
  display: none;
}

.column > p > .margin-toggle:checked + .sidenote,
.column > p > .margin-toggle:checked + .marginnote {
  display: block;
  float: left;
  left: 1rem;
  clear: both;
  width: 95%;
  margin: 1rem 2.5%;
  vertical-align: baseline;
  position: relative;
}

/* ------------------------------------------------------------------------ */
/* Styling for buttons. */
/* ------------------------------------------------------------------------ */
.btn {
  display: inline-block;
  margin-top: 1.4rem;
}
.btn button {
  font-size: 1.4rem;
}
.btn button .icon {
  font-size: 1.2rem;
}

/* ------------------------------------------------------------------------ */
/* Styling for i18n. */
/* ------------------------------------------------------------------------ */

/* reduce zh & ja font-size to match en */

p:lang(zh),
span:lang(zh),
p:lang(ja),
span:lang(ja) {
  font-size: 1.2rem;
}

.sidenote:lang(zh),
.marginnote:lang(zh) {
  font-size: 0.5rem;
}

.sidenote:lang(ja),
.marginnote:lang(ja) {
  font-size: 0.9rem;
}
